@use "base/variables" as v;
@use "base/mixins" as m;
.paquetes {
  &__grid {
    @include m.contenedor;
    @include m.grid(1, 3rem);
    @include m.tablet {
      @include m.grid(3, 3rem);
      align-items: start; 
    }
  }
  &__submit{
    margin-top: 3rem;
    @include m.boton(v.$azul, v.$blanco);
  }
  
}
.paquete{
  @include m.card ;
  &:nth-child(2){
      order: -1;
      @include m.tablet { 
            order: unset;
      }
  }
  &__lista{
      @include m.reset-lista ;
      line-height: 2;
  }
  &__precio{
      font-size: 6rem;
      margin: 4rem 0;
      font-weight: v.$black;
      text-align: center;
      color: v.$azul;

  }
}
