@use "base/variables" as v;
@use "base/mixins" as m;
.formulario {
  display: flex;
  flex-direction: column;
  &__fieldset {
    border: none;
    margin-top: 1rem;
    padding: 0;
  }
  &__legend {
    color: v.$gris;
    font-size: 2.2rem;
    margin-bottom: 1rem;
  }

  &__campo {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 1.5rem 0;
  }
  &__contenedor-icono {
    display: flex;
    border-radius: 0.5rem;
    overflow: hidden;
  }
  &__icono {
    background-color: v.$gris-oscuro;
    width: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      color: v.$blanco;
      font-size: 2rem;
    }
  }
  &__label {
    display: block;
    font-size: 1.8rem;
    color: rgba($color: v.$negro, $alpha: 0.8);
  }
  &__input,
  &__select {
    padding: 1rem;
    border: 1px solid darken($color: v.$gris, $amount: 20);
    border-radius: 0.5rem;

    &--file {
      border: none;
    }
    &--sociales {
      @extend .formulario__input;
      flex: 1;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
  &__texto {
    margin-top: 2rem;
  }
  &__imagen {
    max-width: 20rem;
  }
  &__radio {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: space-around;

    
  }
  
  &__listado {
    @include m.tags;
  }
  &__submit {
    @include m.boton(v.$azul, v.$blanco);
    width: auto;
    align-self: flex-start;
    &--registrar, &--full {
      width: 100%;
    }
    
  }
}
