@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta{
    padding: 1.5rem 1.5rem 1.5rem 3rem;
    margin: 1rem 0;
    font-size: 1.2rem;
    text-transform: uppercase;
    &__error{
        font-weight: v.$bold;
        background-color: lighten($color: v.$rojo, $amount: 60%);
        border-left: .5rem solid v.$rojo;
                
    }

    &__exito{
        background-color: lighten($color: v.$verde, $amount: 30%);
        border-left: .5rem solid v.$verde;
        font-weight: v.$bold;

    }
}