@use "base/variables" as v;
@use "base/mixins" as m;

.bloques {
  @include m.contenedor;
  &__grid {
    @include m.grid(1, 3rem);
    @include m.desktop {
      @include m.grid(2, 3rem);
    }
  }
}
.bloque {
  padding: 2rem;

  border-radius: 1rem;
  &:nth-child(1) {
    @include m.bg-gradient(#e8c31f);
  }
  &:nth-child(2) {
    @include m.bg-gradient(#4b1fe8);
  }
  &:nth-child(3) {
    @include m.bg-gradient(#1fc0e8);
  }
  &:nth-child(4) {
    @include m.bg-gradient(#1fe84b);
  }
  &__heading{
    margin: 1rem 0 2rem 0;
    font-size: 3.5rem;
  }
  &__texto{
    margin: 1rem 0;
    &--cantidad{
        margin-top: 5rem;
        font-weight: v.$black;
        font-size: 4rem;
        text-align: center;
    }
  }
}
